@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

* {
font-family: 'Press Start 2P', cursive;
}

body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0;
    background-color: black;
}

code {
    font-family: 'Press Start 2P', cursive;
}

.App {
    text-align: center;
}

.grid {
    background-color: black;
    border: 2px solid #fff;
    display: grid;
    grid-template-columns: repeat(20, 1fr);
    grid-template-rows: repeat(20, 1fr);
    height: 600px;
    margin: 5% auto;
    width: 600px;
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
}

.cell {
    border: 2px solid rgba(276, 276, 276, 0.2);
}

.snake {
    background-color: green;
}

.food {
    background-color: red;
}

.game-over {
    font-size: 72px;
    font-weight: 700;
    margin-top: 16px;
    color: gold;
    position: fixed;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
  }
  

/* CSS code */
.start-button {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
    z-index: 1;
  }
  
  
  .start-button:hover {
    background-color: #45a049;
  }

.restart-button {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
    z-index: 1;
  }
  
  
  .restart-button:hover {
    background-color: #45a049;
  }
  
  
  .go-back-button {
    position: fixed;
    top: 18%;
    left: 10%;
    padding: 10px 20px;
    font-size: 16px;
    background-color: #d62525;
    color: white;
    border: none;
    cursor: pointer;
    z-index: 1;
  }

  .score {
    color: white;
    position: fixed;
    top: 8%;
    left: 8%;
  }

